

<!--table-->

<template>
	<div id="mytable">
		  <!---->
		   <div class="block" style="padding-top:20px;">
		     <el-pagination
		       @size-change="handleSizeChange"
		       @current-change="handleCurrentChange2"
		       :current-page="currentPage"
		       :page-sizes="page_sizes"
		       :page-size="100"
		       layout="total, sizes, prev, pager, next, jumper"
		       :total="total">
	  	     </el-pagination>
		   </div>
	</div>

</template>

<script>
	 export default {
     name:"tem_page",
		    data() {
		      return {
		      	currentRow:null
		      }
		    },
		    props:{
		    	currentPage:{//
		    		type:Number,
		    		default:function(){
		    			return 1;
		    		}
		    	},
		    	total:{//
		    		type:Number,
		    		default:function(){
		    			return 6;
		    		}
		    	},
		    	page_sizes:{//
		    		type:Array,
		    		default:function(){
		    			return [6];
		    		}
		    	}

		    },
		     methods: {
		      handleEdit(index,row){//
		      	this.$emit("handleEditTWO",index,row);
		      },
		      handleDelete(index,row){//
		      	this.$emit("handleDeleteTWO",index,row);
		      },
		      handleSizeChange(val) {
		        console.log(` ${val} `);
		      },
		      handleCurrentChange(val){
		      	this.currentRow = val; //table
		      	this.$emit("handleCurrentChange",val);
		      },
		      handleCurrentChange2(val){//handleCk
		      	this.$emit("handleCk",val);//
		      }
		    },
		  }

</script>

<style>


</style>



