<!--wangeditortempalte-->

<template>
	<div>
		<div id="myWangEditor" ref="myWangEditor" style="margin-bottom:10px;">

		</div>
		<el-button type="primary" @click="getHtml"></el-button>
		<!-- <el-button type="info" @click="getText">text</el-button>
    <el-button @click="closeDialoge"> </el-button> -->
	</div>
</template>



<script>
	import wangEditor from 'wangeditor';

	export default({
		name:"tem_WangEditor",
		data(){
			return {
				editor:""
			}
		},
		props:{
			uploadFileName:{//files.myFileName;
                type:String,
                default:'myFileName'
            },
           uploadImgMaxSize:{//  3M
           		type:Number,
           		default:3 * 1024 * 1024
           },
           uploadImgMaxLength:{//  1 
           		type:Number,
           		default:1
           },
           withCredentials:{// cookie  withCredentials
           		type:Boolean,
           		default:true
           },
           uploadImgTimeout:{// timeout 3
           		type:Number,
           		default:3000
           },
           uploadImgServer:{//
           		type:String,
           		default:"http://localhost:3000/artical/wangeditor/upload"
           },
           uploadImgParams:{////  <=v3.1.0  encode  encode//  >=v3.1.1  encode  encode
           		type:Object,
           		default:function(){
           			return {
	           			token: 'abcdef12345'
	           		};
           		}
           },
           pasteIgnoreImg:{//true
           		type:Boolean,
           		default:true
           },
           colors:{//
           		type:Array,
           		default:function(){
           			return ['#000000',
					        '#eeece0',
					        '#1c487f',
					        '#4d80bf',
					        '#c24f4a',
					        '#8baa4a',
					        '#7b5ba1',
					        '#46acc8',
					        '#f9963b',
					        '#ffffff']
           		}
           },
           emotions:{ //
           		type:Array,
           		default:function(){
           			return [{
				            // tab 
				            title: '',
				            // type -> 'emoji' / 'image'
				            type: 'image',
				            // content -> 
				            content: [
				                {
				                    alt: '[]',
				                    src: 'http://img.t.sinajs.cn/t4/appstyle/expression/ext/normal/50/pcmoren_huaixiao_org.png'
				                },
				                {
				                    alt: '[]',
				                    src: 'http://img.t.sinajs.cn/t4/appstyle/expression/ext/normal/40/pcmoren_tian_org.png'
				                }
				            ]
				        },
				        {
				            // tab 
				            title: 'emoji',
				            // type -> 'emoji' / 'image'
				            type: 'emoji',
				            // content -> 
				            content: ['?', '@', '#', '!', '$']
				        }]
           		}
           },
           fontNames:{//
           		type:Array,
           		default:function(){
           			return ['','','Arial','Tahoma','Verdana']
           		}
           }
		},
		created(){

		},
		mounted(){
			var _this = this;
			_this.editor = new wangEditor(this.$refs.myWangEditor);
			_this.editor.customConfig = {
				//onchange : (html) =>{this.editorContent = html;} ,
				uploadImgMaxSize : this.uploadImgMaxSize,
				uploadImgMaxLength : this.uploadImgMaxLength ,
				uploadFileName : this.uploadFileName ,
				withCredentials : this.withCredentials ,
				uploadImgTimeout : this.uploadImgTimeout ,
				uploadImgServer : this.uploadImgServer ,
				uploadImgParams:this.uploadImgParams,
				pasteIgnoreImg:this.pasteIgnoreImg,
				colors:this.colors,
				emotions:this.emotions,
				onchange:this.editorChange,
				onfocus :this.editorOnfocus ,
				onblur :this.editorOnblur
			};

			this.toListenUp(_this.editor);//
			_this.editor.create();
		},
		methods:{
			toListenUp:function(editor){
				var _this = this;
				editor.customConfig.uploadImgHooks = {
				    before: function (xhr, editor, files) {
				    },
				    success: function (xhr, editor, result) {
				    },
				    fail: function (xhr, editor, result) {
				    },
				    error: function (xhr, editor) {
				    },
				    timeout: function (xhr, editor) {

				    },
				    customInsert: function (insertImg, result, editor) {
				        var url = result.data[0]
				        insertImg(url)
				    }
				    }
			},
			editorChange:function(html){//
				this.$emit("tochange",html);
			},
			editorOnfocus:function(){//
				this.$emit("tofocus");
			},
			editorOnblur:function(html){
				this.$emit("toblur",html);
			},
			getHtml:function(){
				var html = this.editor.txt.html();
				//console.log(html)
				this.$emit("getHtml",html);
			},
			getText:function(){
				var text = this.editor.txt.text();
				this.$emit("getText",text);
      },
      closeDialoge:function(){
        this.$emit("closeDialoge")
      }
		}

	})
</script>

<style scoped>

</style>



