var slis = function(th) {



    var handler = function(captchaObj) {
        captchaObj.appendTo('#captcha');
        captchaObj.onReady(function() {
            $("#wait").hide();
        });
        $('#btn').click(function() {
                var result = captchaObj.getValidate();
                if (!result) {
                    return alert('');
                }
                $.ajax({
                    //url: 'http://localhost:3000/users/login', //
                    url: 'http://47.96.65.156:3000/users/login', //
                    type: 'POST',
                    dataType: 'json',
                    xhrFields: { //session
                        withCredentials: true
                    },
                    data: {
                        name: $('.username').val(),
                        password: $('.password').val(),
                        geetest_challenge: result.geetest_challenge,
                        geetest_validate: result.geetest_validate,
                        geetest_seccode: result.geetest_seccode
                    },
                    success: function(data) {
                        var redata = data
                        if (redata.state > 0) { //
                            th.$cookies.set("user", redata.data._id);
                            th.$cookies.set("userAvatar", redata.data.avatar);
                            th.$router.push({ path: '/index/content' }); //
                        } else if (redata.state < 0) {
                            alert(redata.msg);
                            captchaObj.reset();
                        }
                    }
                });
            })
            // http://docs.geetest.com/install/client/web-front/
    };


    $.ajax({
        //url: "http://localhost:3000/users/gt/register-slide?t=" + (new Date()).getTime(), // 
        url: "http://47.96.65.156:3000/users/gt/register-slide?t=" + (new Date()).getTime(), // 
        type: "get",
        dataType: "json",
        success: function(data) {

            //  initGeetest 
            // 1
            // 2
            initGeetest({
                //  4 
                gt: data.gt,
                challenge: data.challenge,
                offline: !data.success, // 
                new_captcha: data.new_captcha, // 

                product: "float", // floatpopup
                width: "300px"
                    // http://docs.geetest.com/install/client/web-front/
            }, handler);
        }
    });

};
export default slis;


